.App {
  text-align: center;
}

.Loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh; /* Full viewport height to center vertically */
  font-size: 24px; /* Adjust the font size as needed */
  color: #fff; /* Optional: color for the text */
  background-color: #282c34; /* Optional: background color for contrast */
}

.BR-image {
  width: 40%;
  height: auto;
  max-width: 100%;
  margin: 8px 0;
  object-fit: contain;
  border-radius: 15px;
}


.BR-header {
  background-color: #61DAFB;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.BR-link {
  color: #fff;
}

.ID-image {
  width: 80%;
  height: auto;
  max-width: 100%;
  margin: 8px 0;
  object-fit: contain;
  border-radius: 15px;
}


.ID-header {
  background-image: url(https://h5.5696slots3.com/splash.a59dd.jpg);
  background-repeat: no-repeat;
  background-size: cover; 
  background-position: center; 
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.ID-link {
  color: #fff;
}



